import {bootstrap} from '@angular/platform-browser-dynamic';
import {enableProdMode} from '@angular/core';
import {HTTP_PROVIDERS} from '@angular/http';
import {MediaPlannerAppComponent, environment} from './app';
import {PlannerService} from './app/shared';

if (environment.production) {
    enableProdMode();
}

bootstrap(MediaPlannerAppComponent, [
    HTTP_PROVIDERS,
    PlannerService
]);
